.point {
    background-color: #282c34;
    padding: 15px;
    text-align: center;
    overflow: hidden;
    width: 60px;
    height: 60px;
    cursor: pointer;
    user-select: none;
    font-size: 20px;
    color: black;
}

.point.black {
    background: black!important;
}

.point.grey {
    filter: grayscale(1);
}